.nav {
  display: flex;
  flex-direction: column;
  padding: calc(var(--offset) * 1.2) 0;
  width: 100%;
}
@media (max-width: 999px) {
  .nav {
    padding: var(--offset) 0;
  }
}
.nav__top {
  display: flex;
  align-items: center;
  gap: var(--offset);
  margin-bottom: calc(var(--offset) * 0.5);
}
@media (max-width: 999px) {
  .nav__top {
    justify-content: space-between;
    position: relative;
    z-index: 10;
  }
}
@media (max-width: 999px) {
  .nav__top .btn {
    display: none;
  }
}
.nav__bottom {
  width: 100%;
}
@media (max-width: 999px) {
  .nav__bottom {
    transition-duration: 0.5s;
    transition-property: transform;
    transform: translateX(-100%);
    position: fixed;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    gap: 1em;
    top: 0;
    left: 0;
    z-index: 5;
    background-color: var(--color-bg-secondary);
  }
}
.nav__bottom.open {
  transform: translateX(0);
}
.nav__bottom-list {
  display: flex;
  align-items: center;
  gap: calc(var(--offset) * 3);
}
@media (max-width: 999px) {
  .nav__bottom-list {
    flex-direction: column;
    align-items: flex-start;
    margin-top: calc(var(--offset) * 6);
    width: 100%;
    gap: 0;
    overflow-y: auto;
  }
}
.nav__bottom-list li {
  width: 100%;
}
.nav__bottom-list li a {
  font-weight: 700;
  font-size: calc(var(--fz) * 0.9);
  color: var(--color-text-primary);
  transition-duration: 0.5s;
  transition-property: color;
}
@media (max-width: 999px) {
  .nav__bottom-list li a {
    font-size: calc(var(--fz) * 1.2);
    padding: 0.7em 0 0.7em 1em;
    width: 100%;
    display: block;
    border-bottom: 0.1em solid var(--color-text-secondary);
  }
}
.nav__bottom-list li a:hover, .nav__bottom-list li a:focus, .nav__bottom-list li a:focus-within {
  color: var(--color-text-secondary);
}
.nav__logo {
  display: flex;
  align-items: center;
  gap: var(--offset);
  margin-right: auto;
}
@media (max-width: 999px) {
  .nav__logo {
    margin: 0;
  }
}
.nav__logo:hover span, .nav__logo:focus span, .nav__logo:focus-within span {
  color: var(--color-text-primary);
}
.nav__logo:hover img, .nav__logo:focus img, .nav__logo:focus-within img {
  transform: scale(0.95);
}
.nav__logo img {
  width: calc(var(--offset) * 3);
  height: calc(var(--offset) * 3);
  object-fit: contain;
  transform-origin: center;
  transform: scale(1);
  transition-duration: 0.5s;
  transition-property: transform;
}
.nav__logo span {
  max-width: calc(var(--offset) * 14);
  font-weight: 500;
  font-size: calc(var(--fz) * 0.75);
  line-height: 1.2;
  color: var(--color-text-secondary);
  transition-duration: 0.5s;
  transition-property: color;
}
@media (max-width: 480px) {
  .nav__logo span {
    display: none;
  }
}
.nav__network {
  display: flex;
  align-items: center;
  gap: calc(var(--offset) * 0.5);
  margin-right: calc(var(--offset) * 2);
}
@media (max-width: 999px) {
  .nav__network {
    display: none;
  }
}
.nav__network:hover svg, .nav__network:focus svg, .nav__network:focus-within svg {
  fill: var(--color-text-secondary);
}
.nav__network:hover span, .nav__network:focus span, .nav__network:focus-within span {
  color: var(--color-text-secondary);
}
.nav__network:hover b, .nav__network:focus b, .nav__network:focus-within b {
  color: var(--color-text-secondary);
}
.nav__network svg {
  width: calc(var(--offset) * 0.8);
  height: calc(var(--offset) * 0.8);
  fill: var(--color-bg-dark-blue);
  transition-duration: 0.5s;
  transition-property: fill;
}
.nav__network span {
  font-weight: 500;
  font-size: calc(var(--fz) * 0.8);
  line-height: 130%;
  color: var(--color-text-primary);
  transition-duration: 0.5s;
  transition-property: color;
}
.nav__network b {
  font-weight: 700;
  font-size: calc(var(--offset) * 0.85);
  line-height: 150%;
  color: var(--color-bg-dark-blue);
  transition-duration: 0.5s;
  transition-property: color;
}
.nav-lang-dropdown {
  position: relative;
}

.nav__bottom-footer {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  gap: var(--offset);
  margin-top: auto;
  padding: var(--offset);
}
@media (min-width: 999px) {
  .nav__bottom-footer {
    display: none;
  }
}

.nav__bottom-contacts .nav__network {
  display: flex;
  margin-top: 0.7em;
}
.nav__bottom-contacts .nav__network span {
  font-size: calc(var(--fz) * 1.2);
}
.nav__bottom-contacts .nav__network svg {
  width: calc(var(--fz) * 1.5);
  height: calc(var(--fz) * 1.5);
}

.nav__bottom-cocials {
  display: flex;
  align-items: center;
  gap: 1em;
}
.nav__bottom-cocials a {
  width: calc(var(--offset) * 2.5);
  height: calc(var(--offset) * 2.5);
  border-radius: 50%;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  transition-property: box-shadow;
  transition-duration: 0.5s;
}
.nav__bottom-cocials a:hover, .nav__bottom-cocials a:focus, .nav__bottom-cocials a:focus-within {
  box-shadow: 0 0 0 0.05em var(--color-bg-dark-blue);
}
.nav__bottom-cocials a svg {
  height: var(--offset);
  width: calc(var(--offset) * 1.1);
}

.nav__btn {
  display: none;
  border: none;
  box-shadow: 0 0 0 0.1em #e9e9e9;
  border-radius: 50%;
  width: calc(var(--offset) * 2.4);
  height: calc(var(--offset) * 2.4);
  padding: 0.6em 0.4em;
  transition: transform 0.5s ease;
  transform: scale(1) translateZ(0);
  will-change: transform;
  overflow: hidden;
  backface-visibility: hidden;
  z-index: 5;
  position: relative;
}
.nav__btn > span {
  position: relative;
  width: 80%;
  height: 70%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.nav__btn > span::after, .nav__btn > span::before {
  content: "";
  display: block;
  width: 100%;
  height: 0.15em;
  border-radius: 5em;
  background-color: var(--color-text-primary);
  position: absolute;
  transition: background-color 0.5s, top 0.3s ease 0.3s, bottom 0.3s ease 0.3s, transform 0.3s ease;
}
.nav__btn > span::after {
  top: 0;
}
.nav__btn > span::before {
  bottom: 0;
}
.nav__btn > span > span {
  display: block;
  width: 100%;
  height: 0.15em;
  border-radius: 1rem;
  background-color: var(--color-text-primary);
  transition: background-color 0.5s, opacity 0.3s ease;
}
@media (max-width: 999px) {
  .nav__btn {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }
}

.nav__btn.open > span::after {
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
  transition: top 0.3s ease, transform 0.3s ease 0.3s;
}

.nav__btn.open span::before {
  bottom: 50%;
  transform: translateY(50%) rotate(-45deg);
  transition: bottom 0.3s ease, transform 0.3s ease 0.3s;
}

.nav__btn.open span > span {
  opacity: 0;
  transition: opacity 0.3s ease;
}

.nav__btn:hover {
  transform: scale(0.95);
}

@media (min-width: 999px) {
  .nav-menu {
    display: none;
  }
}

.btn-lang {
  font-weight: 700;
  font-size: calc(var(--offset) * 0.8);
  color: var(--color-bg-dark-blue);
  padding: 0.7em;
  box-shadow: 0 0 0 0.1em #e9e9e9;
  border-radius: 2em;
  transition-duration: 0.5s;
  transition-property: color, box-shadow;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.3em;
}
@media (max-width: 999px) {
  .btn-lang {
    font-size: var(--fz);
  }
}
.btn-lang svg {
  width: 0.7em;
  height: 0.7em;
  transition: transform 0.3s ease-in-out;
}
.btn-lang:hover, .btn-lang:focus, .btn-lang:focus-within {
  box-shadow: 0 0 0 0.05em var(--color-bg-dark-blue);
}

.link-desc {
  display: flex;
  flex-direction: column;
}

.dropdown {
  display: flex;
  flex-direction: column;
  gap: 0.5em;
  position: absolute;
  z-index: 10;
  top: calc(var(--offset) * 2.5);
  left: 50%;
  transform: translateX(-50%);
  border-radius: 1.4em;
  padding: 0.7em 1.5em 0.7em 0.9em;
  box-shadow: 0 0 0 0.05em var(--color-bg-dark-blue);
}
@media (max-width: 999px) {
  .dropdown {
    top: calc(var(--offset) * 3);
  }
}
.dropdown-item {
  font-weight: 600;
  font-size: var(--fz);
  letter-spacing: -0.04em;
  color: var(--color-bg-dark-blue);
}
.dropdown-link {
  transition-duration: 0.5s;
  transition-property: color;
}
.dropdown-link:hover, .dropdown-link:focus, .dropdown-link:focus-within {
  color: var(--color-text-secondary);
}

.nav-lang-dropdown[open] .btn-lang svg {
  transform: rotate(180deg);
  transition: transform 0.3s ease-in-out;
}

.btn {
  width: fit-content;
  font-weight: 700;
  font-size: calc(var(--fz) * 0.9);
  text-align: right;
  color: #fff;
  padding: 0.7em 1.4em;
  border-radius: 2em;
  background-color: var(--color-bg-dark-blue);
  box-shadow: 0 0 0 0.1em var(--color-bg-dark-blue);
  transition-duration: 0.5s;
  transition-property: color, background-color, box-shadow;
}
.btn:hover, .btn:focus, .btn:focus-within {
  background-color: #fff;
  color: var(--color-bg-dark-blue);
}

