}
.news__inner {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.news__card {
  width: 45%;
  height: auto;
}
.news__card img {
  object-fit: fill;
  width: 100%;
  height: 60%;
}
.news__card h1 {
  width: 100%;
  font-weight: 700;
  font-size: calc(var(--fz) * 1.2);
  line-height: 112%;
}
.news__card p {
  font-weight: 400;
  color: var(--color-text-secondary);
  font-size: calc(var(--fz) * 0.9);
}

.footer {
  position: relative;
  background-color: var(--color-bg-secondary);
  margin-top: calc(var(--offset) * 4);
}
@media (max-width: 999px) {
  .footer__container {
    display: none;
  }
  .footer__form {
    width: 100%;
    border-radius: 0;
    position: relative;
    text-align: center;
  }
  .footer__form-container {
    padding-right: calc(var(--offset) * 4);
  }
  .footer__form form {
    width: 100%;
    align-items: center;
  }
  .footer__form form input {
    width: calc(var(--offset) * 25);
  }
  .footer__form-details {
    display: block;
  }
  .footer__form-socials {
    display: flex;
  }
  .footer__form-image {
    display: flex;
  }
}
.footer__container {
  display: flex;
  flex-direction: column;
  gap: calc(var(--offset) * 3.8);
  padding: var(--offset-xl);
  width: 100%;
}
@media (max-width: 1280px) {
  .footer__container {
    padding: calc(var(--offset) * 4);
    padding-bottom: calc(var(--offset) * 6);
  }
}
@media (max-width: 480px) {
  .footer__container {
    padding: var(--offset);
  }
}
.footer__top {
  width: 60%;
  display: flex;
  gap: calc(var(--offset) * 4);
  align-items: center;
}
.footer__top-image {
  width: 20%;
  display: flex;
  justify-content: center;
}
.footer__top-image img {
  width: calc(var(--offset) * 5);
}
.footer__top ul {
  width: 60%;
  height: 60%;
  display: flex;
  align-self: flex-start;
  flex-wrap: wrap;
  align-items: center;
  font-weight: 700;
  gap: calc(var(--offset) * 2);
}
.footer__bottom {
  width: 60%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: calc(var(--offset) * 4);
}
.footer__bottom-socials {
  width: 20%;
  display: flex;
  justify-content: space-around;
}
.footer__bottom-socials div {
  width: calc(var(--offset) * 3);
  height: calc(var(--offset) * 3);
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: var(--offset);
  background-color: white;
  border-radius: 25px;
  border: 1px solid #fff;
}
.footer__bottom-socials svg {
  width: calc(var(--offset));
  height: calc(var(--offset));
}
.footer__bottom-details {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.footer__bottom-address {
  max-width: calc(var(--fz) * 10);
  line-height: 125%;
}
.footer__bottom-address h2 {
  font-size: calc(var(--offset));
}
.footer__bottom-address p {
  font-size: calc(var(--offset) * 0.8);
}
.footer__bottom-contacts h2 {
  font-size: calc(var(--offset));
}
.footer__bottom-contacts a {
  margin-top: 12px;
}
.footer__form {
  position: absolute;
  background-color: var(--color-text-blue);
  width: 38%;
  display: flex;
  align-items: center;
  height: calc(100% + var(--offset) * 3);
  border-radius: 50% 0 0 0;
  right: 0;
  bottom: 0;
}
.footer__form-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: var(--offset-xl);
  padding-left: calc(var(--offset-xl) * 1.2);
  padding-right: calc(var(--offset-xl));
  padding-bottom: calc(var(--offset-xl) * 0.2);
  width: 100%;
}
@media (max-width: 1280px) {
  .footer__form-container {
    padding: calc(var(--offset) * 4);
    padding-left: calc(var(--offset) * 5);
    padding-right: 0;
    padding-bottom: calc(var(--offset) * 0.5);
  }
}
@media (max-width: 480px) {
  .footer__form-container {
    padding: calc(var(--offset) * 3);
  }
}
.footer__form h1 {
  font-size: calc(var(--offset) * 1.5);
  color: white;
}
.footer__form p {
  font-weight: 400;
  color: var(--color-bg-secondary);
  font-size: calc(var(--fz) * 0.8);
  margin-bottom: calc(var(--offset));
}
.footer__form form {
  width: 60%;
  display: flex;
  flex-direction: column;
}
.footer__form input {
  background-color: #068dd9;
  box-shadow: none;
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.45);
  padding-bottom: calc(var(--offset) * 0.5);
  margin-bottom: calc(var(--offset));
}
.footer__form input::placeholder {
  font-weight: 500;
  color: white;
  font-size: calc(var(--fz) * 0.7);
  opacity: 0.5;
}
.footer__form button {
  margin-top: var(--offset);
  background-color: white;
  color: var(--color-bg-dark-blue);
  border: none;
  box-shadow: none;
}
.footer__form-details {
  display: none;
}
.footer__form-details p {
  opacity: 0.5;
}
.footer__form-socials {
  display: none;
  justify-content: center;
}
.footer__form-socials div {
  width: calc(var(--offset) * 2);
  height: calc(var(--offset) * 2);
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  border-radius: 25px;
  border: 1px solid #fff;
}
.footer__form-socials div:nth-child(1) {
  margin-right: var(--offset);
}
.footer__form-socials svg {
  width: calc(var(--offset));
  height: calc(var(--offset));
}
.footer__form-image {
  display: none;
  justify-content: center;
  margin-top: calc(var(--offset) * 2);
}
.footer__form-image img {
  width: calc(var(--offset) * 3);
  height: calc(var(--offset) * 3);
}

body {
  background-color: var(--color-bg-primary);
  color: var(--color-text-primary);
  font-family: var(--ffN), sans-serif;
  overflow-x: hidden;
  touch-action: manipulation;
  user-select: auto;
  font-size: var(--fz);
  width: 100%;
}

body.open {
  overflow: hidden;
}

main {
  overflow: hidden;
}

:root {
  /* colors */
  --color-default-white: #fff;
  --color-default-black: #000;
  --color-bg-primary: #ffffff;
  --color-bg-secondary: #f7f7f7;
  --color-bg-dark-blue: #1f3c88;
  --color-bg-blue: #068dd9;
  --color-text-primary: #010306;
  --color-text-secondary: #848487;
  --color-text-black: #000000;
  --color-text-blue: #068dd9;
  --ffN: "Nunito";
  --fz: max(calc(1 * 1vw), 15px);
  --offset: max(calc(1.25 * 1vw), 15px);
  --offset-xxxl: calc(var(--offset) * 10);
  --offset-xxl: calc(var(--offset) * 8);
  --offset-xl: calc(var(--offset) * 7);
  --offset-l: calc(var(--offset) * 6);
  --offset-m: calc(var(--offset) * 5);
  --offset-s: calc(var(--offset) * 4);
  --hh: calc(var(--offset) * 4);
  --offset-grid: calc(var(--offset) * 4);
}

* {
  box-sizing: border-box;
}
*:focus {
  outline: none;
}
* *::before,
* *::after {
  box-sizing: inherit;
}

.container {
  display: flex;
  flex-direction: column;
  padding: 0 var(--offset-xl);
  width: 100%;
}
@media (max-width: 1280px) {
  .container {
    padding: 0 calc(var(--offset) * 2);
  }
}
@media (max-width: 480px) {
  .container {
    padding: 0 var(--offset);
  }
}

::selection {
  background-color: var(--violet);
  color: var(--white);
}

::-webkit-scrollbar {
  width: 0 !important;
  height: 0 !important;
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  border-radius: 50px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  text-wrap: balance;
  user-select: text;
}

p {
  user-select: text;
  text-wrap: balance;
}

details {
  display: block;
}

summary {
  list-style: none;
  cursor: pointer;
}

summary::-webkit-details-marker {
  display: none;
}

a {
  text-decoration: none;
  color: inherit;
  outline: none;
  text-wrap: balance;
  user-select: text;
  user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  -webkit-tap-highlight-color: transparent;
  touch-action: manipulation;
  outline: none;
}

img {
  display: block;
}

a:hover {
  text-decoration: none;
  color: inherit;
}

button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  font-size: var(--fz);
  padding: 0;
  font-family: "Inter", sans-serif;
  user-select: text;
}
button svg,
button span {
  pointer-events: none;
}

input {
  font-family: var(--ffG), sans-serif;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

textarea {
  resize: none;
}

html {
  scroll-behavior: smooth;
}

[hidden],
[type=hidden],
.visually-hidden,
.tns-visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}